<script>
import Header from "../../components/global/header/HeaderArea.vue"
import { useRouter } from "vue-router"
import { ref, onMounted } from 'vue'
import { getUrlId, monthHandle } from '@/utils/handle'
import { apiGetWorkOrderInfo } from "@/api/api"


export default {
  setup() {
    const router = useRouter()
    let orderData2 = ref([])
    const urlId = getUrlId().urlId

    onMounted(async () => {
      try {
        orderData2.value = await apiGetWorkOrderInfo({ id: urlId })
        orderData2.value = orderData2.value.data

        //處理日期
        let res = monthHandle(orderData2.value.workdays)
        orderData2.value.newDates = res.newDates
        orderData2.value.info = res.info
        orderData2.value.time = res.time
        orderData2.value.monthInfo = Object.keys(orderData2.value.info)
        //eatTypeInfo
        // orderData2.value.disabledPerson.eatTypeInfo = []
        // if (orderData2.value.disabledPerson.eatType?.length > 0) {
        //   orderData2.value.disabledPerson.eatType.forEach(item => {
        //     orderData2.value.disabledPerson.eatTypeInfo.push(item.name)
        //   })
        // }
        //abilityToActInfo
        orderData2.value.disabledPerson.abilityToActInfo = []
        if (orderData2.value.disabledPerson.abilityToAct?.length > 0) {
          orderData2.value.disabledPerson.abilityToAct.forEach(item => {
            orderData2.value.disabledPerson.abilityToActInfo.push(item.name);
          })
        }
        //servicePlaceInfo
        orderData2.value.disabledPerson.servicePlaceInfo = orderData2.value.disabledPerson.servicePlace.city.name + orderData2.value.disabledPerson.servicePlace.area.name + " " + orderData2.value.disabledPerson.servicePlace.place.name
        //serviceInfo
        orderData2.value.employerService.serviceInfo = []
        if (orderData2.value.employerService.service?.length > 0) {
          orderData2.value.employerService.service.forEach(item => {
            orderData2.value.employerService.serviceInfo.push(item.name)
          })
        }
        //看護 languageInfo
        orderData2.value.checkedCaretaker.languageInfo = []
        if (orderData2.value.checkedCaretaker.language?.length > 0) {
          orderData2.value.checkedCaretaker.language.forEach(item => {
            orderData2.value.checkedCaretaker.languageInfo.push(item.name)
          })
        }
        //看護 experienceInfo
        orderData2.value.checkedCaretaker.experienceInfo = []
        if (orderData2.value.checkedCaretaker.experience?.length > 0) {
          orderData2.value.checkedCaretaker.experience.forEach(item => {
            orderData2.value.checkedCaretaker.experienceInfo.push(item.careExperience)
          })
        }
        //看護 workPlaceInfo
        orderData2.value.checkedCaretaker.workPlaceInfo = []
        if (orderData2.value.checkedCaretaker.workPlace?.length > 0) {
          orderData2.value.checkedCaretaker.workPlace.forEach(item => {
            orderData2.value.checkedCaretaker.workPlaceInfo.push(`${item.city}${item.area}`)
          })
        }
        //timePeriod工作時段
        orderData2.value.checkedCaretaker.timePeriodInfo = []
        orderData2.value.checkedCaretaker.timePeriod.forEach(item => {
          orderData2.value.checkedCaretaker.timePeriodInfo.push(item.name)
        })
        orderData2.value.checkedCaretaker.timePeriodInfo.reverse()
        if (orderData2.value.checkedCaretaker.otherJobTime != 0) {
          orderData2.value.checkedCaretaker.timePeriodInfo.push(`${orderData2.value.checkedCaretaker.otherJobTime}小時`);
        }

      } catch (error) {
        console.log(error)
      }
      // console.log("orderData2", orderData2.value)
    })

    return { orderData2 }
  },
  components: {
    Header
  },
};

</script>

<template>
  <Header title="工單內容" backToPath="/order/list" />
  <div class="orderInfo3">
    <section class="c-main">
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">照顧日期時段</div>
          <div class="item-content">
            <div class="main2">
              <div class="title2">照顧日期</div>
              <div class="content2">{{ orderData2?.monthInfo?.[0] }}/{{
                  orderData2?.info?.[orderData2?.monthInfo?.[0]].join('、')
              }}</div>
              <div class="content2" v-if="orderData2?.monthInfo?.length == 2">{{ orderData2?.monthInfo?.[1] }}/{{
                  orderData2?.info?.[orderData2?.monthInfo?.[1]].join('、')
              }}</div>
            </div>
            <div class="main2">
              <div class="title2">照顧時段</div>
              <div class="content2">{{ orderData2.time }}</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData2.checkedCaretaker">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse1">{{
              orderData2.checkedCaretaker.name
          }}</div>
          <div class="item-content collapse" id="collapse1">
            <div>
              <div class="basicInfo">
                <div class="infoText">
                  <div class="main1">
                    <div class="title1">性別</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.gender == 'Male' ? '男性' : '女性' }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">年齡</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.age }}歲</div>
                  </div>
                  <div class="main1">
                    <div class="title1">身高</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.height }}公分</div>
                  </div>
                  <div class="main1">
                    <div class="title1">體重</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.weight }}公斤</div>
                  </div>
                  <div class="main1">
                    <div class="title1">年資</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.seniority }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">國籍</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.countryCode }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">語言</div>
                    <div class="content1">{{ orderData2.checkedCaretaker.languageInfo.join('、') }}</div>
                  </div>
                </div>
                <img :src='orderData2.checkedCaretaker.avatarUrl' alt="看護照片"
                  v-if="(orderData2.checkedCaretaker.avatarUrl)">
                <img src="@/assets/image/noavatar.jpg" alt="無照片" v-if="(!orderData2.checkedCaretaker.avatarUrl)">
              </div>
              <div class="main2">
                <div class="title2">履歷身分</div>
                <div class="content2">{{ orderData2.checkedCaretaker.serviceRequirement.name }}</div>
              </div>
              <div class="main2">
                <div class="title2">具備證照</div>
                <div class="content2">{{ orderData2.checkedCaretaker.license.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">收費標準</div>
                <div class="content2" v-if="orderData2.checkedCaretaker.wage.home">居家：NT${{
                    orderData2.checkedCaretaker.wage.home
                }}/小時<span v-if="orderData2.checkedCaretaker.wage?.fullDay?.home">、NT${{
    orderData2.checkedCaretaker.wage.fullDay.home
}}/日</span></div>
                <div class="content2" v-if="orderData2.checkedCaretaker.wage.home">醫院：NT${{
                    orderData2.checkedCaretaker.wage.hospital
                }}/小時<span v-if="orderData2.checkedCaretaker?.wage?.fullDay?.hospital">、NT${{
    orderData2.checkedCaretaker?.wage.fullDay.hospital
}}/日</span>
                </div>
              </div>
              <div class="main2">
                <div class="title2">照顧經驗及時數</div>
                <div class="content2">{{ orderData2.checkedCaretaker.experienceInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">服務地區</div>
                <div class="content2">{{ orderData2.checkedCaretaker.workPlaceInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">工作時段</div>
                <div class="content2">{{ orderData2.checkedCaretaker.timePeriodInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">自我介紹</div>
                <div class="content2">{{ orderData2.checkedCaretaker.selfIntroduction }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData2.disabledPerson">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse2">被照護者資訊</div>
          <div class="item-content collapse" id="collapse2">
            <div>
              <div class="main1">
                <div class="title1">名稱</div>
                <div class="content1">{{ orderData2.disabledPerson.name }}</div>
              </div>
              <div class="main1">
                <div class="title1">性別</div>
                <div class="content1">{{ orderData2.disabledPerson.gender == 'Male' ? '男性' : '女性' }}</div>
              </div>
              <div class="main1">
                <div class="title1">年齡</div>
                <div class="content1">{{ orderData2.disabledPerson.age }}歲</div>
              </div>
              <div class="main1">
                <div class="title1">身高</div>
                <div class="content1">{{ orderData2.disabledPerson.height }}公分</div>
              </div>
              <div class="main1">
                <div class="title1">體重</div>
                <div class="content1">{{ orderData2.disabledPerson.weight }}公斤</div>
              </div>
              <div class="main1">
                <div class="title1">關係</div>
                <div class="content1">{{ orderData2.disabledPerson.relationship }}</div>
              </div>
              <!-- <div class="main2">
                <div class="title2">是否有傳染力</div>
                <div class="content2">{{ orderData2.disabledPerson.isContagious ? "是" : "否" }}</div>
              </div>
              <div class="main2">
                <div class="title2">飲食類型</div>
                <div class="content2">{{ orderData2.disabledPerson.eatTypeInfo.join('、') }}</div>
              </div> -->
              <div class="main2">
                <div class="title2">行動能力</div>
                <div class="content2">{{ orderData2.disabledPerson.abilityToActInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">照顧地點</div>
                <div class="content2">{{ orderData2.disabledPerson.servicePlaceInfo }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse3">病症</div>
          <div class="item-content collapse" id="collapse3">
            <div class="main2" v-for="item in orderData2?.disabledPerson?.disease" :key="item.id">
              <div class="title2">{{ item.diseaseName }}</div>
              <div class="content2">{{ item.symptom.join('、') }}</div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse4">服務需求</div>
          <div class="item-content collapse" id="collapse4">
            <div class="content2">{{ orderData2.employerService?.serviceInfo?.join('、') }}</div>
          </div>
        </div>
      </div>
    </section>

  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.orderInfo3 {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.basicInfo {
  display: flex;
  justify-content: space-between;

  img {
    height: 10rem;
    width: 7.5rem;
    border-radius: 4px;
  }

}


.title {
  font-size: 1.125rem;
  font-weight: 700;
  color: #000000;
  margin-bottom: 16px;
}


.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;

  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
  margin-top: 10px;
  margin-bottom: 10px;

  .title2 {
    font-size: 14px;
    font-weight: 400;
    color: nth($secondary-color, 1);
  }

  .content2 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }

}
</style>